#Map {
  width: 50vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 50vw;
  z-index: 0; }
.Header-products-center {
  text-align: center;
  font-size: rem(13);
  p {
    margin: 0; } }
.Header-products-service {
  h4 {
    margin: 0;
    font-size: rem(23); }
  h5 {
    font-size: rem(10);
    color: #004f8f;
    margin: 0; }
  figure {
    max-width: rem(50);
    margin: 0; } }
.Platform-Logo {
  margin: 0;
  a {
    max-width: rem(100);
    display: block; }
  svg {
    max-width: 100%; } }
.Platform {
  max-height: 100vh;
  overflow-y: hidden;
  .Platform-product div.Header-products {
    background: #e4e4e4;
    padding: rem(15) rem(15) rem(12); }
  .Hamburguer span, .Hamburguer span:after {
    background: $dark-blue; }
  .orange-text {
    text-shadow: 0 0 0 black; }
  .Header {
    position: relative;
    min-height: inherit;
    .Menu > li > a {
      color: $dark-blue; } }
  .Platform-product {
    position: absolute;
    min-height: 100vh;
    background: white;
    width: 50vw;
    top: 0;
    left: 0;
    form {
      padding: rem(5) rem(10); }
    div {
      padding: rem(4); }
    label {
      background: #004f8f;
      display: block; }
    select {
      max-width: 100%;
      background: #004f8f;
      color: white;
      border: none;
      border-radius: 0;
      font-size: rem(12);
      height: rem(26);
      font-weight: 100; }
    article:nth-child(even) {
      background: #f0f0f0; } } }
.Platform-productContent {}

.Platform-productSection {
  padding: rem(20);
  figure {
    margin: 0;
    max-width: rem(200);
    position: relative;
    svg {
      position: absolute;
      top: 0;
      left: 0;
      max-width: rem(42); } }
  img {
    display: block;
    border-radius: 50%; } }
.Platform-price {
  text-align: center;
  background-image: url("../../img/globo-precio.svg");
  position: absolute;
  background-size: cover;
  height: rem(80);
  font-size: rem(18);
  width: rem(80);
  right: 0;
  top: rem(-20);
  color: white;
  p {
    font-size: rem(18) !important;
    margin: 0 !important; }
  small {
    text-align: center;
    display: block;
    color: white;
    font-size: rem(12); } }
.Availability {
  margin-top: rem(10);
  ul {
    margin: rem(10) 0 0;
    padding: 0;
    font-size: rem(18); }
  li {
    padding: 0 rem(3);
    font-weight: bold;
    color: #cacaca;
    line-height: rem(18);
    &.active {
      color: #004f8f; }
    &:first-child {
      padding: 0; } }
  time {
    font-size: rem(14);
    font-weight: 700;
    color: #004f8f; } }
.Availability-additional {
  margin: 0;
  padding: 0;
  em {
    font-size: rem(18); }
  div {
    border: rem(4) solid  #004f8f;
    border-radius: 50%;
    height: rem(50);
    width: rem(50);
    margin: 0 auto rem(3);
    font-size: rem(21);
    font-weight: 600;
    color: #004f8f; }
  li {
    margin: 0 rem(3);
    max-width: rem(62);
    text-align: center;
    line-height: rem(9); }
  span {
    font-size: rem(8) !important;
    display: block; } }

.Platform-productInfo {
  padding: 0.625rem 0 0 rem(17) !important;
  position: relative;
  text-align: justify;

  h3 {
    margin: 0;
    font-size: rem(20);
    color: #333333;
    font-weight: 600; }
  b {
    font-weight: 400;
    font-size: rem(24); }
  div {
    padding: 0 !important; }
  h5 {
    padding: 0;
    margin: 0;
    font-size: rem(14);
    font-weight: 400; }
  p {
    font-size: rem(14);
    line-height: inherit;
    overflow: hidden;
    max-height: rem(48);
    margin: rem(6) 0 0;
    text-align: justify; }
  span {
    color: #004f8f;
    font-size: rem(15);
    font-weight: 600; }
  a {
    font-size: 1rem;
    color: #004f8f;
    font-weight: 700; } }
.InfoServices {
  display: none;
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0,0,0,.8);
  width: 100vw;
  height: 100vh;
  z-index: 10;
  .owl-carousel {
    width: 100% !important; }
  @include transition(all .5s);

  .Availability-additional {
    .experience {
      font-size: rem(24); }
    div {
      height: 4.125rem;
      width: 4.125rem;
      font-size: rem(30); }
    li {
      margin: rem(10);
      max-width: rem(96); }
    svg {
      transform: scale(1.3); }
    span {
      color: #004f8f;
      font-size: rem(12) !important;
      line-height: rem(14);
      font-weight: 500; } }
  h2 {
    color: #2d2d2d;
    font-weight: 700;
    margin-bottom: rem(3);
    font-size: rem(22);
    margin: 0 rem(8);
    span {
      display: block;
      font-size: .5em;
      color: #004f8f; } }
  span {
    font-weight: 600; }
  h3 {
    margin: 0;
    font-weight: 700;
    font-size: rem(23);
    text-align: right; }
  a {
    margin: rem(12) 0 rem(16);
    display: block;
    font-size: rem(15);
    color: #232323; } }
.InfoServices.show {
  display: flex !important;
  opacity: 1; }
.Form-content {
  max-width: rem(220);
  margin: auto;
  transform: translateX(1rem);
  select {
    width: rem(60); }
  h6 {
    color: #efa43b;
    font-size: rem(14);
    margin: 0 0 rem(10);
    font-weight: 500;
    text-align: center; }
  input[type="text"] {
    max-width: rem(115);
    border-radius: rem(5);
    border: none;
    height: rem(26); }
  button {
    width: rem(150);
    padding: rem(12) 0 rem(12);
    margin: auto;
    display: block;
    border: none;
    border-radius: rem(4);
    background: #efa43b;
    color: white;
    font-weight: 600;
    transform: translateY(1rem); } }
.InfoServices-info {
  position: relative;
  padding: rem(20) rem(28) 0 rem(28);
  overflow: scroll;
  text-align: left;
  max-height: rem(450);
  .pets {
    padding: 0;
    li {
      margin: 0 rem(8); }
    span {
      display: block;
      font-size: rem(12);
      text-align: center;
      color: #636262; } }
  .age {
    margin: rem(6) 0;
    font-size: 1rem;
    color: gray; }
  h4 {
    font-size: rem(20);
    margin: rem(22) 0; }
  p {
    margin: rem(6) 0 1em 0;
    line-height: 1.2; }
  form {
    background: #04508e;
    color: white;
    position: relative;
    border-radius: rem(10);
    margin: rem(25) 0;
    padding-top: rem(18);

    .price {
      position: absolute;
      top: rem(-10);
      left: rem(-10);
      width: rem(101);
      height: rem(118);
      background-image: url("../../img/price.svg");
      background-size: cover;
      color: #04508e;
      font-size: rem(22);
      span {
        display: block;
        font-size: .6em; } } }
  label {
    display: block;
    margin: rem(10) 0;
    text-align: center; }

  #PayForm {
    transition: all 1s ease;
    background: #fff;
    position: absolute;
    top: 0;
    left: 100%;
    width: 100%;
    height: 100%;
    section {
      padding: 0 rem(20) 0 rem(10); }
    .close {
      content: "";
      display: block;
      margin-right: rem(10);
      height: 100%;
      width: rem(40);
      float: left;
      background: #000;
      cursor: pointer;
      opacity: .1;
      padding: rem(10);
      &:hover {
        opacity: .2; } }
    &.show {
      left: 0; } } }
.InfoServices-content {
  position: relative;
  max-width: rem(900);
  width: 100%;
  background: white;
  img {
    vertical-align: top;
    display: block; }
  .owl-buttons {
    position: absolute;
    top: calc(50% - 16px);
    width: 100%;
    text-align: left;
    padding: 0 10px;
    div {
      font-weight: 600;
      font-size: 1.25rem;
      background: #ffffff;
      width: rem(26);
      text-align: center;
      height: rem(28);
      line-height: rem(26);
      color: #ccc;
      border: rem(1) solid #ccc; } }
  .owl-next {
    position: absolute;
    right: rem(10);
    top: 0; } }
.InfoServices-infoInclude {
  span {
    background: #e0e0e0;
    padding: rem(1) rem(9);
    border-radius: rem(14);
    font-size: rem(15);
    font-weight: 500;
    color: #4c4c4c;
    margin: rem(2) rem(1);
    display: inline-block; }
  em {
    font-weight: bold;
    font-style: initial; }
  figure {
    margin: 0;
    background-size: cover;
    background-position: center; }
  p {
    margin: rem(12) 0 rem(9);
    line-height: rem(20); }
  val {
    font-size: rem(24); }
  select, input {
    margin: rem(10) 0;
    background: #eee;
    border: rem(1) solid gray;
    color: gray;
    padding: rem(5) rem(10);
    border-radius: rem(3); }
  .btn {
    background: #004f8f;
    color: white;
    margin: rem(45);
    border: none;
    padding: rem(10) rem(24);
    cursor: pointer;
    font-size: 1.1rem; } }

.InfoServices-stars span {
  color: #ffd909;
  font-size: rem(28);
  margin: rem(12) 0 0; }
.InfoServices-close {
  position: absolute;
  right: rem(-30);
  top: rem(-30);
  z-index: 5;
  color: #ffffff;
  font-size: rem(44);
  cursor: pointer;
  background: #004f8f;
  width: rem(55);
  border-radius: 50%;
  line-height: 1.1;
  height: rem(55); }



.Info-available {
  margin: 0;
  padding: 0;
  li:nth-child(2), li:nth-child(3) {
    font-size: rem(12);
    font-weight: 700;
    line-height: rem(13);
    color: #04508e;
    text-align: center; }
  p {
    margin: rem(2) 0;
    color: #04508e;
    font-weight: 600; }
  span {
    margin-right: rem(4);
    color: #CACACD;
    &.active {
      color: #efa43b; } } }
